*
{
  -webkit-box-sizing:border-box;
          box-sizing:border-box;

}

.Particle
{
  position:absolute;
  top:0px;
  left:0px;
  right:0px;
  bottom:0px;
  z-index:-10;
}

.heading
{
  text-align:center;
}

.primary-head
{
  text-transform:uppercase;
  font-family: 'Eater', cursive;
  letter-spacing:3px;
  font-size:40px;
  margin:5px;

}

.secondary-head
{
  

  font-family: 'Bree Serif', serif;
  letter-spacing:3px;
  font-size:25px;
  margin-top:-10px;
  margin-bottom:30px;

}



.Content-box
{
  margin:0 auto; 
  width:70vw;
  height:1000px;
  background-image:-webkit-gradient(linear , left top, right top , from(rgba(1,87, 155,0.5)) ,color-stop(50%, rgba(2,136,209,0.5)), to(rgba(1,87, 155,0.5)));
  background-image:-webkit-linear-gradient(left , rgba(1,87, 155,0.5) 0% ,rgba(2,136,209,0.5) 50%, rgba(1,87, 155,0.5) 100%);
  background-image:-o-linear-gradient(left , rgba(1,87, 155,0.5) 0% ,rgba(2,136,209,0.5) 50%, rgba(1,87, 155,0.5) 100%);
  background-image:linear-gradient(to right , rgba(1,87, 155,0.5) 0% ,rgba(2,136,209,0.5) 50%, rgba(1,87, 155,0.5) 100%);
  background-size:cover;
  border-radius:5px;

}

.add-field 
{
  background-color:black;
  text-decoration:none;
  height:40px;
  margin:10px;
  width:250px;
  border:none;
  color:white;
  padding:5px;
  font-size:15px;
  border-radius:3px;
  border:1px solid white;



}

.add-button
{
  font-size:12px;
  color: white;
  height:40px;
  background-color:#012c4f;
  border:none;
  padding:5px;
  margin:5px;
  border-radius:3px;
  border:1px solid white;
  cursor: pointer;



}



.filter-field 
{
  background-color:black;
  text-decoration:none;
  height:40px;
  margin:10px;
  width:200px;
  border:none;
  color:white;
  padding:5px;
  font-size:15px;
  border-radius:3px;
  border:1px solid white;


}

.dropdown
{
  background-color:black; 
  color:white;
  border:1px solid white;
  display: inline;


}







  


