.item-box
{
  display:block;
  position:relative;
  margin:6px;
  /*background-image:linear-gradient(to right bottom , rgba(120,50,102,1) 0% ,rgba(249,181,223,0.7) 50%, rgba(120,50,102,1) 100%);*/
  background-image: -webkit-linear-gradient(315deg, #473B7B 0%, #3584A7 51%, #30D2BE 100%);
  background-image: -o-linear-gradient(315deg, #473B7B 0%, #3584A7 51%, #30D2BE 100%);
  background-image: linear-gradient(-225deg, #473B7B 0%, #3584A7 51%, #30D2BE 100%);
}

.content-block
{
  margin:0px;
}

.task
{
  font-family: 'Patua One', cursive;
  letter-spacing:1px;
  color:#BCC9D0;
  font-size:20px;
  margin:3px;
  display:inline-block;
  position: absolute;
  left:5px;
  text-decoration:underline;

}

.tertiary-head
{

font-family: 'Cabin Sketch', cursive;
  display: inline-block;
  font-size:30px;
  padding:0px;
 
}

.btn-block
{
  display: inline-block;
  position: absolute;
  right:5px;
}

.status-style
{
  font-family: 'Patua One', cursive;
  color:black;
  
}




.text_field
{
  background-color:black;
  text-decoration:none;
  height:30px;
  margin:10px;
  width:250px;
  border:none;
  color:white;
  padding:5px;
  font-size:15px;
  border-radius:3px;
  border:1px solid white;




}






.btn, .btn-edit , .btn-status
{
  
   background-color:#011627; 
  border:1px solid white; 
  color: white; 
  padding: 5px 5px; 
  font-size: 15px; 
  cursor: pointer;
  margin:3px;
  width:auto;
  height:auto;
}
